/* global graphql */

const _ = require('lodash');
import React from 'react';
import Breadcrumb from '../components/breadcrumb';
import StandingsSelect from '../components/standings-selector';
import { BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import '../../node_modules/react-bootstrap-table/dist/react-bootstrap-table-all.min.css';

class IndexPage extends React.Component {
	constructor(props) {
			super(props);
			this.teamChangeHandler = this.teamChangeHandler.bind(this);
			this.loadData = this.loadData.bind(this);
			this.state = {
				teamsLabels: [],
				teamSelected: "",
				results: []

			}
			this.options = {
				defaultSortName: 'changeSnap',  // default sort column name
				defaultSortOrder: 'desc'  // default sort order
			};
		}
					
		teamChangeHandler = (event) => {
			var selected = event.target.value;
			this.setState(
				{ teamSelected: selected },
				() => this.loadData()
			)
		}

		positionClickHandler = event => {
			this.loadData();

		}
	
		loadData = () => {
			
			var team = this.state.teamSelected;
			var rb = this.refs.rb.checked;
			var wr = this.refs.wr.checked;
			var te = this.refs.te.checked;
			var search = this.refs.search.value;
			const positions = ['WR', 'RB', 'TE', 'FB'];
			const snaps = this.props.data.allDataJson.edges[0].node.snaps;
			const targets = this.props.data.allDataJson.edges[0].node.targets;
			const receptions = this.props.data.allDataJson.edges[0].node.receptions;
			const rushs = this.props.data.allDataJson.edges[0].node.rushs;
			const teamSnaps = this.state.teamSnaps;
			const teamAttempts = this.state.teamAttempts;
			var results = [];
			var allData = [];

			// Add snap data to All Data
			snaps.map((player) => {
				if (_.some(positions, (el) => _.includes(player.POS, el))) {
					allData.push({
						id: `${player.NAME}-${player.POS}-${player.TEAM}`,
						name: player.NAME,
						pos: player.POS,
						team: player.TEAM,
						week1_snaps: _.defaultTo(_.get(player, 'Week_1', 0),0),
						week2_snaps: _.defaultTo(_.get(player, 'Week_2', 0),0)
					});
				}
			});

			// Add target data
			allData.map((player) => {
				var targetId;
				_.forEach(targets, (target) => {
					if (_.some(positions, (el) => _.includes(target.POS, el))) {
						targetId = `${target.NAME}-${target.POS}-${target.TEAM}`;
						if (targetId === player.id) {
							player.week1_targets = _.defaultTo(_.get(target, 'Week_1', 0),0);
							player.week2_targets = _.defaultTo(_.get(target, 'Week_2', 0),0);
						}
					}
				});
			});

			// Add receptions
			allData.map((player) => {
				var receptionId;
				_.forEach(receptions, (reception) => {
					if (_.some(positions, (el) => _.includes(reception.POS, el))) {
						receptionId = `${reception.NAME}-${reception.POS}-${reception.TEAM}`;
						if (receptionId === player.id) {
							player.week1_utilization = _.defaultTo(_.get(reception, 'Week_1', 0), 0);
							player.week2_utilization = _.defaultTo(_.get(reception, 'Week_2', 0), 0);
						}
					}
				});
			});

			// Add rushing
			allData.map((player) => {
				var rushId;
				_.forEach(rushs, (rush) => {
					rushId = `${rush.NAME}-RB-${rush.TEAM}`;
					if (rushId === player.id) {
						player.week1_utilization += _.defaultTo(_.get(rush, 'Week_1', 0), 0);
						player.week2_utilization += _.defaultTo(_.get(rush, 'Week_2', 0), 0);
					}
				});
			});
			
			var filterPos = [];

			if (te) filterPos.push('TE');
			if (wr) filterPos.push('WR');
			if (rb) filterPos.push('RB');

			// Final Results to print in table 
			allData.map((player) => {
				if (_.toLower(player.name).includes(_.toLower(search))) {	
					if (_.some(filterPos, (el) => _.includes(player.pos, el))) {
						if (_.includes(player.team, team) || team === 'All') {
							const snapTeam = _.find(teamSnaps, ['name', player.team]);
							const targetTeamWeek1 = _.find(teamAttempts, { 'Tm': player.team, 'Week': 1});
							const targetTeamWeek2 = _.find(teamAttempts, { 'Tm': player.team, 'Week': 2});
							const snapPerWeek1 = _.defaultTo((player.week1_snaps / snapTeam.snaps_week1 * 100), 0).toFixed(2);
							const snapPerWeek2 = _.defaultTo((player.week2_snaps / snapTeam.snaps_week2 * 100), 0).toFixed(2);
							const targetPerWeek1 = _.defaultTo((player.week1_targets / targetTeamWeek1.Att * 100), 0).toFixed(2);
							const targetPerWeek2 = _.defaultTo((player.week2_targets / targetTeamWeek2.Att * 100), 0).toFixed(2);
							results.push({
								name: player.name,
								pos: player.pos,
								team: player.team,
								week1: `Snaps: ${snapPerWeek1}%
Target Share: ${targetPerWeek1}%
Utilization: ${player.week1_utilization}`,
								week2: `Snaps: ${snapPerWeek2}%
Target Share: ${targetPerWeek2}%
Utilization: ${player.week2_utilization}`,
								changeSnap: parseFloat((snapPerWeek2 - snapPerWeek1).toFixed(2)), 
								changeTarget: (targetPerWeek2 - targetPerWeek1).toFixed(2), 
							});
						}
					}
				}
			});

			this.setState(
				{ results: results }
			)
		}
	
	UNSAFE_componentWillMount() {
		const snapsData = this.props.data.allDataJson.edges[0].node.snaps;
		const attemptsData = this.props.data.allDataJson.edges[0].node.attempts;
		const teamsUnique = _.uniqBy(snapsData, 'TEAM');
		var teamNames = [];
		teamsUnique.map((player) => {
			teamNames.push(player.TEAM);	
		});
		teamNames.sort();

		// Get snaps for only qbs as they account for all team snaps
		var snapsQB = _.filter(snapsData, { 'POS': 'QB' });

		// Sum all qb snaps from the same team
		var teamSnaps = _(snapsQB)
				.groupBy('TEAM')
				.map((objs, key) => ({
					'name': key,
					'snaps_week1': _.defaultTo(_.sumBy(objs, 'Week_1'),0),
					'snaps_week2': _.defaultTo(_.sumBy(objs, 'Week_2'),0)
				})) 
				.value();

		var helper = {};
		var teamAttempts = attemptsData.reduce((r, o) => {
			var key = o.Tm + '-' + o.Week;
			if (!helper[key]) {
				helper[key] = Object.assign({}, o); // create a copy of o
				r.push(helper[key]);
			} else {
				helper[key].Att += o.Att;
			}
			return r;
		}, []);

		this.setState({ teamSnaps: teamSnaps });
		this.setState({ teamAttempts: teamAttempts });
		this.setState({ teamNames: teamNames });	
	}        
  	
  	componentDidMount() {
		this.loadData()
	}
		
  	render() {

		return (
			
		   <main className="container-fluid">
			     <div className="card mb-3">
			        <div className="card-header"> 
						<form>
							<div className="form-row align-items-center">
								<div className="col-auto">
									<div className="form-check mb-2">
										<input className="form-check-input" type="checkbox" id="rb" ref="rb" onChange={this.positionClickHandler} defaultChecked={true} />
										<label className="form-check-label" >RB</label>
									</div>
								</div>
								<div className="col-auto">
									<div className="form-check mb-2">
										<input className="form-check-input" type="checkbox" id="wr" ref="wr" onChange={this.positionClickHandler} defaultChecked={true} />
										<label className="form-check-label" >WR</label>
									</div>
								</div>
								<div className="col-auto">
									<div className="form-check mb-2">
										<input className="form-check-input" type="checkbox" id="te" ref="te" onChange={this.positionClickHandler} defaultChecked={true} />
										<label className="form-check-label" >TE</label>
									</div>
								</div>
								<StandingsSelect data={this.state.teamNames} change={(event) => this.teamChangeHandler(event)} />
								<div className="col-auto">
									<div className="input-group mb-2">
										<input className="form-control" type="text" id="search" placeholder="Player name" ref="search" onChange={this.positionClickHandler} />
									</div>
								</div>			
							</div>
						</form>
						
					  </div>
			        <div className="card-body">
			          <div className="table-responsive">
						  
							<BootstrapTable data={this.state.results} options={this.options} striped hover version='4'>
							<TableHeaderColumn isKey dataField='name' isKey={true} dataSort={true} width="15%" >Name</TableHeaderColumn>
							<TableHeaderColumn dataField='pos' dataSort={ true } width="7%">Pos</TableHeaderColumn>
							<TableHeaderColumn dataField='team' dataSort={true} width="7%" >Team</TableHeaderColumn>
							<TableHeaderColumn tdStyle={{ whiteSpace: 'pre-wrap' }} thStyle={{ whiteSpace: 'normal' }} dataField='week1' width="10%" >Week 1</TableHeaderColumn>
							<TableHeaderColumn tdStyle={{ whiteSpace: 'pre-wrap' }} thStyle={{ whiteSpace: 'normal' }} dataField='week2' width="10%">Week 2</TableHeaderColumn>
							<TableHeaderColumn dataField='changeSnap' dataSort={true} width="10%" >Change Snap</TableHeaderColumn>
							<TableHeaderColumn dataField='changeTarget' dataSort={true} width="10%" >Change Target</TableHeaderColumn>
						</BootstrapTable>
			          </div>
			        </div>
			        <div className="card-footer small text-muted"></div>
			      </div> 
		   </main>);  
	}
}
export default IndexPage;
 
export const pageQuery = graphql`
  query standingsQuery {
		allDataJson {
			edges {
				node {
					snaps {
						NAME
						POS
						TEAM
						Week_1
						Week_2
					}
					targets {
						NAME
						POS
						TEAM
						Week_1
						Week_2
					}
					receptions {
						NAME
						POS
						TEAM
						Week_1
						Week_2
					}
					rushs {
						NAME
						TEAM
						Week_1
						Week_2
					}
					attempts {
						Tm
						Week
						Att
					}
				} 
			}
		}
}
`;



